import React,{Component} from 'react';
import axios from 'axios';
import SuggestionDisplay from './SuggestBlock';

var surl="https://resterapp.herokuapp.com/restaurantList";

class SuggestionLogic extends Component{
    constructor(){
        super()

        this.state = {
            suggestions:""
        }
    }
    render(){
        return(
            <div>
                <SuggestionDisplay sugstdata = {this.state.suggestions}/>
            </div>
        )
    }
    componentDidMount(){
        let date = new Date().getDate()
        let mealtype = sessionStorage.getItem('mealtype')
        let sugurl;
        if(date>0 && date<11){
            sugurl = `${surl}/${mealtype}?hcost=1000&lcost=500`
        }else if(date>10 && date<21){
            sugurl = `${surl}/${mealtype}?hcost=800&lcost=300`
        }else if(date>20 && date<32){
            sugurl = `${surl}/${mealtype}?hcost=600&lcost=100`
        }
        axios.get(sugurl)
        .then((response)=> {this.setState({suggestions:response.data})})
    }
}

export default SuggestionLogic;